import { fileUrlToBlob, request } from './utils';
import db from '../db';

import { BookData } from './types';

/**
 * 
 * @param isbn ISBN
 */
export function scrapeByIsbn(isbn: string) {
  return db.settings.querySettingByKey('isbnDataUrl').then(res => {
    const _url = res?.value;
    if (!_url || typeof _url !== 'string') {
      return Promise.reject('isbnDataUrl is invalid');
    }
    const url = _url.replace('{isbn}', isbn);
    return url;
  }).then(url => {
    return request<BookData>(url, {
      method: 'GET',
      mode: 'cors',
    }).then(res => {
      const { covers } = res;
      const coverUrl = new URL(covers[0], url).href;
      return fileUrlToBlob(coverUrl).then(coverBolb => {
        return {
          ...res,
          covers: [coverBolb]
        };
      });
    });
  });
}
