import Compressor from 'compressorjs';

/**
 * fetch  ts 
 * @param args fetch 
 */
export function request<T>(...args: Parameters<typeof fetch>) {
  return fetch(...args).then(res => {
    if (res.status !== 200) {
      return Promise.reject(res);
    }
    return res.json() as Promise<T>;
  });
}

/**
 * 
 * @param imageFile 
 */
export function compressImage(imageFile: File | Blob) {
  return new Promise<File | Blob>((resolve, reject) => {
    new Compressor(imageFile, {
      maxWidth: 600,
      maxHeight: 900,
      quality: 0.8,
      success(file) {
        resolve(file);
      },
      error(err) {
        reject(err);
      }
    });
  });
}

/**  blob */
export function fileUrlToBlob(url: string) {
  return fetch(url).then(res => res.blob()).then(rawBlob => {
    return compressImage(rawBlob);
  });
}

/**  blob  */
export function fileUrlToBlobUrl(url: string) {
  return fileUrlToBlob(url).then(blob => URL.createObjectURL(blob));
}
